import React, { useState, useEffect } from "react"
import axios from "axios"
import './App.css';
import { Link, BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import FilmDetail from "./components/film/FilmDetail";

function App() {
      const [film, setFilm] = useState( [])

      useEffect( () => {
            axios({
                  method: "GET",
                  url: 'http://127.0.0.1:8000/api/films/',

            }).then(response => {
                  setFilm(response.data)
            })
      }, [])
      return (
          <div>
                <ul className="films">
                      {film.map((f) => {
                            return(
                                <Router>
                                    <div className="item">
                                          <img src={f.image} className="img"></img>
                                          <Link className="link-film" to={{ pathname: `/films/${f.id}/`}}>{f.name}</Link>

                                          <h2 className="filmInfo">{f.year}</h2>
                                          <h2 className="filmInfo">{f.rating}</h2>
                                    </div>
                                    <Switch>
                                        <Route path="/films/:id" exact={FilmDetail}></Route>
                                    </Switch>
                                </Router>
                            )
                      })}

                </ul>
          </div>
      );
}


export default App;
